import React,{useState} from 'react';
import Cardlist from './component/Cardlist';
import Cart from './component/Cart';
import './App.css';
import Total from './component/Total';

const App = () => {
  const [items,setItems] = useState(0);
  const [carts,setCarts] = useState(false);
  const [temp,setTemp ] = useState(false);
  const [total,setTotal] = useState(0);
  const [MRP,setMRP] = useState(0);

  const Tot = () => {
    let tot = 0,mrp=0;
    carts ? (
      carts.map((elem) => {
        tot += elem['dprice'] * elem['quantity'];
        mrp += elem['oprice'] * elem['quantity'];
      }),setMRP(mrp),
    setTotal(tot)) : null
  }
  const handle = () => {
     temp ? setTemp(false) : setTemp(true)
  }
  return (
    <div>
      <div id='container'> 
        <div>Shopping Hub</div>
        <div><button onClick={handle} style={{backgroundColor:'black',outline:'none',borderRadius:'1px',cursor:'pointer',border:'2px solid black'}}><img src={require('./cartimg.png')} width='27px'/><sup style={{fontSize:'23px',color:'white'}}>{carts.length}</sup> </button></div>
      </div>
      {temp ? (carts ? (<div id='cart'>  {carts.map((elem) => {return (<Cart Elemdata={elem} key={elem['id']-1} Tot={Tot} setCarts={setCarts}/>)})}  <Total total={total} MRP={MRP} carts={carts} /></div>) : (<div id='cartEmp'>Cart is empty</div>)) : null}
      <Cardlist setItems={setItems} setCarts={setCarts} Tot={Tot} />
    </div>
  )
}

export default App;



